import $ from 'zepto';
import UI from './core';
import toastTpl  from './toast.tpl';

// 默认参数
var defaults = {
		content: '操作提示',
		stayTime: 1000,
		type: 'info',
		position: 'top',
		callback: function() {}
	}
	// 构造函数
	// var _tpl =  dialogTpl(defaults);
	// console.log(_tpl);
// var toast = function(el, option, isFromTpl) {
var toast = function(option) {
	var self = this;
	this.option = $.extend(defaults, option);
	console.log('---toastTpl--');
	var el = toastTpl(self.option);
	this.element = $(el).appendTo('body');
	// this._isFromTpl = isFromTpl;
	// console.log($(el))
	this.elementHeight = self.element.height() || 41;
	if (self.option.position == 'middle') {
		console.log('--middle go-');
		// var thisStayTime = 2;//this.option.stayTime/1000;
		self.element.css({
			// '-webkit-transition': 'opacity .5s', //+this.option.stayTime,
			'opacity': '0'
		});
	} else {
		self.element.css({
			'-webkit-transform': 'translateY(-' + this.elementHeight + 'px)'
			// '-webkit-transform': 'translateY(100px)'
		});
		console.log('-3-top hide--'+self.elementHeight)
	}
	setTimeout(function() {
		self.element.css({
			'-webkit-transition': 'all .5s'
		});
		self.show();
	}, 20);
	return self.element;
}
toast.prototype = {
	show: function() {
		var self = this;
		
		self.element.trigger($.Event('toast:show'));
		if (self.option.position == 'middle') {
			this.element.css({
				'-webkit-transition': 'opacity 1s',
				'opacity': '1'
			});
		} else {
			this.element.css({
				'-webkit-transform': 'translateY(0px)'
			});
		}
		self.option.callback('show');
		
		if (self.option.stayTime > 0) {
			setTimeout(function() {
				self.hide();
			}, self.option.stayTime)
		}
	},
	hide: function() {
		var self = this;
		self.element.trigger($.Event('toast:hide'));
		if (self.option.position == 'middle') {
			this.element.css({
				'opacity': '0'
			});
		} else {
			this.element.css({
				'-webkit-transform': 'translateY(-' + this.elementHeight + 'px)'
			});
		}
		setTimeout(function() {
			self.element.remove();
		}, 500);
	}
}

function Plugin(option) {
	// return $.adaptObject(this, defaults, option, _toastTpl, toast, 'toast');
	return new toast(option);
}
$.fn.toast = $.toast = Plugin;

UI.toast = toast;
module.exports = toast;



