// 端外工具函数
// ===================================================

import queryString from 'query-string';
// import $ from 'zepto';
// import UI from './core';

let utils = {};

/**
 * 端外CID是用 UUID 生成，统计组需要
 * 生产UUID，标示本台机器
 * @returns {*}
 */
utils.uuid = function() {
  var _rnds = new Array(16);
  var _rng = function () {
    for (var i = 0, r; i < 16; i++) {
      if ((i & 0x03) === 0) r = Math.random() * 0x100000000;
      _rnds[i] = r >>> ((i & 0x03) << 3) & 0xff;
    }
    return _rnds;
  };
  // Maps for number <-> hex string conversion
  var _byteToHex = [];
  for (var i = 0; i < 256; i++) {
    _byteToHex[i] = (i + 0x100).toString(16).substr(1);
  }
  // **`unparse()` - Convert UUID byte array (ala parse()) into a string**
  function unparse(buf, offset) {
    var i = offset || 0,
      bth = _byteToHex;
    return bth[buf[i++]] + bth[buf[i++]] +
      bth[buf[i++]] + bth[buf[i++]] + '-' +
      bth[buf[i++]] + bth[buf[i++]] + '-' +
      bth[buf[i++]] + bth[buf[i++]] + '-' +
      bth[buf[i++]] + bth[buf[i++]] + '-' +
      bth[buf[i++]] + bth[buf[i++]] +
      bth[buf[i++]] + bth[buf[i++]] +
      bth[buf[i++]] + bth[buf[i++]];
  }

  var rnds = _rng();
  // Per 4.4, set bits for version and `clock_seq_hi_and_reserved`
  rnds[6] = (rnds[6] & 0x0f) | 0x40;
  rnds[8] = (rnds[8] & 0x3f) | 0x80;
  return unparse(rnds);
};


/**
 * cookie
 * @type {{get: Function, set: Function, unset: Function}}
 */

/**
 * 获取 cookie
 * @param name
 * @returns {*}
 */
utils.getCookie = function(name) {
  var cookieName = encodeURIComponent(name) + '=';
  var cookieStart = document.cookie.indexOf(cookieName);
  var cookieValue = null;
  var cookieEnd;

  if (cookieStart > -1) {
    cookieEnd = document.cookie.indexOf(';', cookieStart);
    if (cookieEnd == -1) {
      cookieEnd = document.cookie.length;
    }
    cookieValue = decodeURIComponent(document.cookie.substring(cookieStart +
      cookieName.length, cookieEnd));
  }

  return cookieValue;
};

/**
 * 设置 cookie
 * @param name
 * @param value
 * @param expires
 * @param path
 * @param domain
 * @param secure
 */
utils.setCookie = function(name, value, expires, path, domain, secure) {
  var cookieText = encodeURIComponent(name) + '=' +
    encodeURIComponent(value);

  if (expires instanceof Date) {
    cookieText += '; expires=' + expires.toUTCString();
  }

  if (path) {
    cookieText += '; path=' + path;
  }

  if (domain) {
    cookieText += '; domain=' + domain;
  }

  if (secure) {
    cookieText += '; secure';
  }

  document.cookie = cookieText;
};

/**
 * 删除cookie
 * @param name
 * @param path
 * @param domain
 * @param secure
 */
utils.unsetCookie = function(name, path, domain, secure) {
  utils.getCookie(name, '', new Date(0), path, domain, secure);
};

// 图片预加载
utils.imgLoad = function (url, callback) {
  var img = new Image();
  //img.crossOrigin = 'anonymous';
  img.src = url;

  if (img.complete) {
    callback(img);
  } else {
    img.onload = function () {
      callback(img);
      img.onload = null;
    };
  }
};

/**
 * 进入可视区
 * @param element
 * @param options 距离位置
 * @returns {boolean}
 */
utils.isInView = function(element, options) {
  var $element = $(element);
  var visible = !!($element.width() || $element.height()) &&
    $element.css('display') !== 'none';

  if (!visible) {
    return false;
  }

  var windowLeft = $win.scrollLeft();
  var windowTop = $win.scrollTop();
  var offset = $element.offset();
  var left = offset.left;
  var top = offset.top;

  options = $.extend({topOffset: 0, leftOffset: 0}, options);

  return (top + $element.height() >= windowTop &&
  top - options.topOffset <= windowTop + $win.height() &&
  left + $element.width() >= windowLeft &&
  left - options.leftOffset <= windowLeft + $win.width());
};


// 解析 data 参数
utils.parseOptions = function(string) {
  if ($.isPlainObject(string)) {
    return string;
  }

  var start = (string ? string.indexOf('{') : -1);
  var options = {};

  if (start != -1) {
    try {
      options = (new Function('',
        'var json = ' + string.substr(start) +
        '; return JSON.parse(JSON.stringify(json));'))();
    } catch (e) {
    }
  }

  return options;
};

// handle multiple browsers for requestAnimationFrame()
// http://www.paulirish.com/2011/requestanimationframe-for-smart-animating/
// https://github.com/gnarf/jquery-requestAnimationFrame
utils.rAF = (function() {
  return window.requestAnimationFrame ||
    window.webkitRequestAnimationFrame ||
    window.mozRequestAnimationFrame ||
    window.oRequestAnimationFrame ||
    // if all else fails, use setTimeout
    function(callback) {
      return window.setTimeout(callback, 1000 / 60); // shoot for 60 fps
    };
})();


// handle multiple browsers for cancelAnimationFrame()
utils.cancelAF = (function() {
  return window.cancelAnimationFrame ||
    window.webkitCancelAnimationFrame ||
    window.mozCancelAnimationFrame ||
    window.oCancelAnimationFrame ||
    function(id) {
      window.clearTimeout(id);
    };
})();

// 解析参数
// https://github.com/sindresorhus/query-string
// parse(string) ? # location.search location.hash
// stringify (object, [options])
// extract (string)
utils.queryStringParse = queryString.parse;
utils.queryStringify = queryString.stringify;

/**
 * url params isappinstalled
 * 0 未安装 return false
 * 1 安装 return true
 */
utils.getIsAppInstalled = function () {
  let params = this.queryStringParse(location.search);
  return !!(params['isappinstalled'] == 1 || 0);
};


/**
 * 检查是否是空对象
 */
utils.isEmpty = function (object) {
  for (var name in object) {
    if (object.hasOwnProperty(name)) {
      return false;
    }
  }
  return true;
};

utils.templateCompile = function(tpl, data){
  return template.compile(tpl)(data);
}

export default utils;



