// init.js
// ======================================

import $ from 'zepto';
import jsKit from 'jsKit';

// 检测端内和端外环境
$(function() {
  const $html = $('html');

  // native
  $html.addClass('native');
  window.ENV = 'native'; // 兼容专题的 环境

  if ($.os.android) {
    $html.addClass('android');
  }

  if ($.os.ios) {
    $html.addClass('ios');
  }

  if (isWeChatBrowser()) {
    $html.addClass('wechat');
  }

  if (jsKit.hasNativeSupport === false) {
    require('./browser');
    $html.removeClass('native');
    $html.addClass('browser');
    window.ENV = 'browser'; // TODO: 去掉 兼容专题的 环境
  }

  jsKit.onDeviceReady(()=> {
    // 夜间模式
    if (jsKitStorage.getItem('settings_nightMode')) {
      window.nightMode = true; // TODO: 专题使用,准备干掉
      $html.addClass('ui-night');
    }

    // 无图模式
    if (jsKitStorage.getItem('settings_imageMode')) {
      // ios 蜂窝下 才使用无图模式
      if ($.os.ios && commonApi.getNetworkInfo().type == 'wifi') {
        return;
      }
      window.noPicMode = true; // TODO: 专题使用,准备干掉
      $html.addClass('ui-nopic');
    }

    // 字体设置
    // 0 默认字体
    // 1 中号字体
    // 2 大号字体
    let fontSize = jsKitStorage.getItem('settings_fontSize');
    if (fontSize === 1) {
      $html.addClass('ui-font-md');
    } else if (fontSize === 2) {
      $html.addClass('ui-font-lg');
    }
  });

  function isWeChatBrowser() {
    return (/MicroMessenger/i).test(window.navigator.userAgent);
  }
});



