import $ from 'zepto';
import UI from './core';
import dialogTpl  from './dialog.tpl';

var dialog={};

// 默认参数
var defaults = {
		title: '',
		content: '',
		button: ['确认'],
		select: 0,
		eventBind: 'click',
		type: 0,
		callback: function() {}
	}
	// 构造函数
var Dialog = function(option) {
	var self = this;
	this.option = $.extend(defaults, option);
	var el = dialogTpl(self.option);
	this.element = $(el).appendTo('body');
	this.button = self.element.find('[data-role="button"]');
	// this.button_no = $(el).find('[data-role="button_no"]');
	// console.log(self.button);
	this._bindEvent();
	this.toggle();
	return self.element;
}
Dialog.prototype = {
	//绑定相关操作事件
	_bindEvent: function() {
		var self = this;
		self.button.on(self.option.eventBind, function(e) {
			var $index = $(e.target);
			var index = $(self.button).index($index);
			self.option.callback('button', index);
			console.log(index);
			var e = $.Event('dialog:action');
			e.index = index;
			self.element.trigger(e);
			self.hide.apply(self);
		});
	},
	toggle: function() {
		if (this.element.hasClass('show')) {
			this.hide();
		} else {
			this.show();
		}
	},
	show: function() {
		var self = this;
		self.option.callback('show');
		self.element.trigger($.Event('dialog:show'));
		self.element.addClass('show');

	},
	hide: function() {
		var self = this;
		self.option.callback('hide');
		self.element.trigger($.Event('dialog:hide'));
		self.element.removeClass('show');
		self.element.remove();
	}
}

function Plugin(option) {
	return new Dialog(option);
}
$.fn.dialog = $.dialog = Plugin;

UI.dialog = dialog;
module.exports = dialog;



